import mockData from "./mockData.json";

import { DatatableRecord } from "@/components/VDatatable/index.types";

import { groupRecords } from "./scripts/groupRecords";
import { APIOptions } from "./index.types";

const latency = () => Math.random() * 500 + 300; // simulate network latency of 300-800ms

export const API = {
  load: async (
    pageNumber = 1,
    pageSize = 25,
    { groupBy }: APIOptions = {}
  ): Promise<DatatableRecord[]> =>
    new Promise((resolve) => {
      const auxiliary = {} as any;
      let records = mockData as DatatableRecord[];

      if (groupBy) {
        auxiliary.groupBy = groupBy;
        records = groupRecords(records, groupBy);
      }

      const data = {
        ...auxiliary,
        totalRecords: records.length,
        pageNumber,
        pageSize,
        records,
      };

      setTimeout(() => resolve(data), latency());
    }),
};
